body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    min-width: 700px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

html {
    position: fixed;
    height: 100%;
    overflow: hidden;
}
  
body {
    margin: 0;
    width: 100vw; 
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'PT Sans', sans-serif;
}

.navbar {
    background: #ffffff;
    height: calc(40px + 3vh);
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.2rem;
    position: fixed;
    width: 100vw;
    top: 0;
    z-index: 999;
}
  
.navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: inherit;
    width: 100%;
}

.navbar-logo {
    margin-left: 2vw;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    height: 100%;
}

.navbar-logo-img {
    height: 90%;
}

.fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    height: inherit;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem;
}

.nav-item {
    height: inherit;
}

.nav-links {
    color: #000;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
}

.nav-links:hover {
    border-bottom: 4px solid #000;
    transition: all 0.2s ease-out;
}

.fa-bars {
    color: #000;
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {
    .NavbarItems {
        position: relative;
    }

    .nav-item {
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        width: 0;
        height: 94vh;
        margin-right: 0;
        position: fixed;
        top: calc(40px + 3vh);
        right: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: #e4e4e4;
        right: 0;
        width: 100%;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }

    .nav-links:hover {
        background-color: #cfcfcf;
        color: #242424;
        border-radius: 0;
    }

    .navbar-logo {
        display: flex;
        justify-self: flex-start;
        align-self: right;
        padding-right: 2%;
    }

    .menu-icon {
        display: flex;
        align-self: center;
        cursor: pointer;
        margin-right: 2rem;
        color: black;
    }
}
