.home-section {
    width: 100vw;
    display: flex;
    flex-direction: column;
}

.home-section-container {
    margin: 3vh 5vw;
    max-height: inherit;
    width: 90vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.home-section-content-container {
    width: inherit;
    display: grid;
}

.home-section-content-container.v1 {
    grid-template-columns: [col1] 60% [col2] auto;
    grid-column-gap: 3%;
    -webkit-column-gap: 3%;
            column-gap: 3%;
}

.home-section-content-container.v2 {
    grid-template-columns: [col1] auto [col2] 60%;
    grid-column-gap: 3%;
    -webkit-column-gap: 3%;
            column-gap: 3%;
}

.home-section-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.home-section-content.v1 {
    grid-column-start: col1;
}

.home-section-content.v2 {
    grid-column-start: col2;
}

.home-section-content > br {
    line-height: calc(10px + 1vh);
}

.home-section-content > h1 {
    text-align: left;
    font-size: calc(11px + 1.5vw);
}

.home-section-content > p {
