    font-size: calc(8px + 0.8vw);
}

.home-section-content-img-container {
    display: flex;
    justify-content: center;
    object-fit: contain;
}

.home-section-content-img-container.v1 {
    grid-column-start: col2;
}

.home-section-content-img-container > img {
    max-width: 100%;
    height: auto;
    object-fit: contain;
}

.home-section-btn {
    max-width: 60%;
}

.home-section-subtitle {
    width: inherit;
    display: flex;
    justify-content: center;
    align-items: center;
}

.home-section-subtitle > h1 {
    text-align: center;
