}

.footer-link-items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 2.5vw;
    text-align: left;
    width: 33%;
    box-sizing: border-box;
}

.footer-link-items > h2 {
    margin-bottom: 3vh;
    color: #fff;
    font-size: min(calc(8px + 1.7vw), 20px);
}

.footer-link-items a {
    font-size: min(calc(10px + 0.8vw), 16px);
    color: #fff;
    text-decoration: none;
    margin-bottom: 0.5rem;
}

.footer-link-items a:hover {
    color: #e9e9e9;
    transition: 0.3s ease-out;
}

